@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&display=swap);

:root{
    --lightsout-bg: #000000;
    --lightsout-bg-wrapper: #15181C;
    --lightsout-text-gray: #818181;
    --lightsout-hover-color:rgb(63, 63, 63);
    --lightsout-container-hover-color:rgb(30, 30, 30);
    --lightsout-container-hover-color-dark: rgba(217, 217, 217, 0.1);

    --twitter-color-main: #1D9BF0;
    --twitter-color-main-hover: #1d8fdb;

    --tweet-font-size: 15px;
    --br-rounded: 20px;
    --br-circle: 100px;

    --border: 1px solid rgb(50, 50, 50);

    --transition-duration: .2s ease;

    --container-padding: 20px;
    --gap: 8px;

    --profile-pic-size: 42px;

    --header-height: 52px;
}

*{
    font-family: 'Open Sans', sans-serif;
    color: #FFFFFF;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

img{
    border-radius: 100px;
    border-radius: var(--br-circle);
    object-fit: cover;
}

body{
    background-color: #000000;
    background-color: var(--lightsout-bg);
    width: 100%;
    display: flex;
    justify-content: center;
    overflow-y: scroll;
    
}

/* ----------------------------------------- */

.HomePage, .ExplorePage, .NotificationsPage, .MessagesPage, .Bookmarks, .ListsPage, .ProfilePage {
    width: 1320px;
    display: flex;
    justify-content: center;
}


.leftNavigation{
    min-width: 322px;
    border-right: 1px solid rgb(50, 50, 50);
    border-right: var(--border);
}

.rightNavigation{
    min-width: 392px;
    /* flex: 1.75; */
    border-left: 1px solid rgb(50, 50, 50);
    border-left: var(--border);
    padding: 0 20px;
    padding: 0 var(--container-padding);
    position: sticky;
    top: -100px;
    bottom: 0;
    z-index: 998;
}
.centerNavigation{
    /* flex: 3; */
    min-width: 606px;
}


.postTweetContainer, .tweetTextNavigation{
    border-bottom: 1px solid rgb(50, 50, 50);
    border-bottom: var(--border);
}


/* Explore Page */

.stickySearchBarWrapper{
    position: sticky;
    top: 0;
    z-index: 999;
    display: flex;
    width: 100%;
    background-color: #000000;
    background-color: var(--lightsout-bg);
    border-bottom: 1px solid rgb(50, 50, 50);
    border-bottom: var(--border);
}

.SettingsOutlinedIcon{
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin: 0 10px;
}



/* See more button */

.seeMoreWrapper{
    cursor: pointer;
    padding: 15px 20px;
    padding: 15px var(--container-padding);
}
.seeMoreWrapper:hover{
    background-color: rgb(30, 30, 30);
    background-color: var(--lightsout-container-hover-color);
}

.seeMore{
    color: #1D9BF0;
    color: var(--twitter-color-main);
}

.MainBtnNav{
    min-width: 318px;
    position: fixed;
    top: 0;
}
.MainBtnNavWrapper{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: var(--container-padding);
    
}

.logoWrapper{
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    cursor: pointer;
    padding: 0 var(--container-padding);
    margin-bottom: 10px;
}

.btnsWrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.btnWrapper{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    align-items: center;
    padding: 10px var(--container-padding) 10px 10px;
    margin: 2px 0;
    cursor: pointer;
}
.btnWrapper:hover{
    background-color: var(--lightsout-bg-wrapper);
    border-radius: var(--br-circle);
}
.btnWrapper > svg{
    margin: 0 10px;
}
.btnWrapper > span{
    font-size: 18px;
    margin-left: 10px;
    
}


.btnTweet{
    width: 100%;
    font-size: 18px;
    font-weight: 700;
    margin-top: 20px;
    background-color: var(--twitter-color-main);
    padding: 16px;
    border-radius: var(--br-circle);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: background-color var(--transition-duration);
}
.btnTweet:hover{
    background-color: var(--twitter-color-main-hover);
}
/* ------------------------------------------------- */

.btnProfileWrapper{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    grid-gap: 18px;
    gap: 18px;
    padding: 10px;
    cursor: pointer;
    
}
.btnProfileWrapper:hover{
    background-color: var(--lightsout-bg-wrapper);
    border-radius: var(--br-circle);
}

.btnProfile{
    display: flex;
    grid-gap: 12px;
    gap: 12px;
}

.btnProfilePic{
    width: 38px;
    height: 38px;
}

.btnProfileInfoWrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.btnProfileName{
    font-size: 15px;
    font-weight: 700;
}

.btnProfileUsername{
    font-size: 13px;
    font-weight: 300;
    color: var(--lightsout-text-gray);
    
}


.btnWrapper.profile{
    position: relative;
}


.ProfilePopUp{
    width: 215px;
    position: absolute;
    bottom: 0px;
    left: 0px;
    background-color: var(--lightsout-bg-wrapper);
    z-index: 9999;
    font-size: var(--tweet-font-size);
    box-shadow: 0px 0px 5px 0px rgba(255, 255, 255, 0.5);
}

.ProfilePopUpWrapper{

}

.ProfilePopUpUpperPartWrapper{
    border-bottom: var(--border);
    padding-bottom: 2px;
    margin-bottom: 2px;
}
.ProfilePopUpLowerPartWrapper{

}

.ProfilePopBtn{
    display: flex;
    align-items: center;
    padding: 12px var(--container-padding);
    grid-gap: 10px;
    gap: 10px;
}
.ProfilePopBtn:hover{
    background-color: var(--lightsout-hover-color);

}

.ProfilePopSpan{

}
.ProfilePopIcon{
}
.SearchBar{
    margin: 6px 0;
    /* padding-top: 6px; */
    width: 100%;
    /* position: fixed; */
}

.searchBarWrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.searchBarInput{
    height: calc(var(--header-height) - 9px);
    width: 100%;
    background-color: var(--lightsout-bg-wrapper);
    border: none;
    outline: none;
    padding-left: 50px;
    border-radius: var(--br-circle);
    border: 1px solid var(--lightsout-bg-wrapper);
}
.searchBarInput:focus{
    background-color: transparent;
    border: 1px solid var(--twitter-color-main);
}

.searchBarIcon{
    position: absolute;
    left: 22px;
}

.headerWrapper{
    z-index: 999;
    height: var(--header-height);
    cursor: pointer;
    position: relative;
}

.header{
    min-width: 606px;
    position: fixed;
    top: 0;
    padding: 0 var(--container-padding);
    border-bottom: var(--border);
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--lightsout-bg);
}

.headerTextSpan{
    cursor: pointer;
}

.headerIcon{
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    cursor: pointer;
    grid-gap: 10px;
    gap: 10px;
    
}
.headerIconIcon{
    border-radius: var(--br-circle);
    transition: background-color var(--transition-duration);
}

.headerIconIcon:hover{
    background-color: var(--lightsout-container-hover-color-dark);
}
.postTweetContainer{
    display: flex;
    grid-gap: var(--gap);
    gap: var(--gap);
    padding: var(--container-padding);
}

.postInput{
    width: 100%;
    display: flex;
    flex-direction: column;
}

.postProfilePic{
    width: var(--profile-pic-size);
    height: var(--profile-pic-size);
}

.postInputTextBar{
    font-size: 1.25rem;
    width: 100%;
    height: 3rem;
    background: none;
    border: none;
    outline: none;
}

.postBtnsWrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.tweetBtnWrapper{
    display: flex;
    align-items: center;
}

.postBtns{
    display: flex;
    align-items: center;
    grid-gap: 18px;
    gap: 18px;
}
.postBtnIcon{
    border-radius: var(--br-circle);
    padding: 2px;
    transition: background-color var(--transition-duration);
}

.postBtnIcon:hover{
    background-color: var(--lightsout-hover-color);
}

.tweetSpan{
    font-size: 12px;
    color: var(--lightsout-text-gray);
}

.postBtnTweet{
    background-color: var(--twitter-color-main);
    border: none;
    font-size: 18px;
    font-weight: 700;
    padding: 6px 18px;
    border-radius: var(--br-circle);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: background-color var(--transition-duration);
}

.tweetTextNavigation{
    cursor: pointer;
    padding: var(--container-padding);
    font-size: var(--tweet-font-size);
}

.tweetTextNavigationWrapper{
    display: flex;
    grid-gap: var(--gap);
    gap: var(--gap);
}
.tweetTextHeader{
    display: flex;
    justify-content: center;
}

.tweetProfilePic{
    width: var(--profile-pic-size);
    height: var(--profile-pic-size);
}

.tweetUserInfo{
    width: 100%;
    display: flex;
    align-items: flex-end;
    grid-gap: 6px;
    gap: 6px;
    margin: 2px 0px 6px 0px;
}


/* ----------------------------------------------- */

.MentionReplyingToWrapper{
    display: flex;
    flex-direction: row;
    grid-gap: 4px;
    gap: 4px;
    margin-top: -6px;
    margin-bottom: 2px;
}

.MentionReplyingToText{
    color: var(--lightsout-text-gray);
}

.MentionReplyingToUsername{
    color: var(--twitter-color-main);
}
.MentionReplyingToUsername:hover{
    text-decoration: underline;
}

/* ----------------------------------------------- */


.tweetText{
    word-wrap: break-word;
}

.tweetUsername, .tweetBullet, .tweetTimeAgo{
    color: gray;
}
.tweetName{
    font-weight: 700;
}
.tweetName:hover, .tweetTimeAgo:hover{
    text-decoration: underline;
}

/* ----------------------------------------------- */

.tweetBtnsWrapper{
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px
}

.tweetBtn{
    padding: 6px;
    display: flex;
    align-items: center;
    border-radius: var(--br-circle);
}
.tweetBtn:hover{
    background-color: var(--lightsout-hover-color);
}

.TrendingContainer{
    width: 100%;
    margin-top: 10px;
}

.trendingContainerWrapper{
    background-color: var(--lightsout-bg-wrapper);
    border-radius: var(--br-rounded);
    overflow:hidden;
}

.trendingHeaderWrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 20px 0px 20px;
    margin-bottom: 10px;
}

.trending{
    display: flex;
    flex-direction: column;
    cursor: pointer;
    padding: 10px var(--container-padding);
}
.trending:hover{
    background-color: var(--lightsout-container-hover-color);
}

.trendingSpanCountry{
    font-size: 12px;
    color: var(--lightsout-text-gray);
}
.trendingSpanName{
    font-weight: 700;
    
}
.trendingSpanNoOfTweet{
    font-size: 12px;
    color: var(--lightsout-text-gray);

}
.WhoToFollow{
    width: 100%;
    margin: 10px 0;
}

.whoToFollowWrapper{
    background-color: var(--lightsout-bg-wrapper);
    border-radius: var(--br-rounded);
    overflow:hidden;
}

.whoToFollowHeader{
    display: flex;
    align-items: center;
    padding: 20px 20px 0px 20px;
    margin-bottom: 10px;
}

.whoToFollowAccountWrapper{
    padding: 10px var(--container-padding);
}

.whoToFollowAccountWrapper:hover{
    background-color: var(--lightsout-container-hover-color);
}

.whoToFollowAccountProfilePic{
    width: var(--profile-pic-size);
    height: var(--profile-pic-size);
}

/* ------------------------------------------ */

.whoToFollowAccountWrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.whoToFollowAccount{
    display: flex;
    grid-gap: var(--gap);
    gap: var(--gap);
    cursor: pointer;
}
.whoToFollowAccountInfo{
}

.whoToFollowAccountNameWrapper{
    display: flex;
    grid-gap: var(--gap);
    gap: var(--gap);
}
.whoToFollowAccountName{
    font-weight: 700;
}
.whoToFollowAccountName:hover{
    text-decoration: underline;
}

.whoToFollowAccountUsername{
    font-size: 14px;
    color: var(--lightsout-text-gray);
}

.whoToFollowAccountFollowBtn{
    font-size: 14px;
    font-weight: 700;
    background-color: #FFF;
    color: #000;
    padding: 8px 12px;
    border-radius: var(--br-circle);
    cursor: pointer;
    transition: background-color var(--transition-duration);
}
.whoToFollowAccountFollowBtn:hover{
    background-color: rgb(204, 204, 204);
}
.ExploreHeaderImg{
    width: 100%;
    border-top: var(--border);
    border-bottom: var(--border);
    cursor: pointer;
}

.exploreHeaderImgWrapper{
    position: relative;
}

.exploreHeaderImg{
    border-radius: 0;
    width: 100%;
    position:relative;
    display:inline-block;
}

.exploreHeaderImgWrapper:after {
    content:'';
    position: absolute;
    left:0; top:0;
    width:100%; height:100%;
    display:inline-block;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 25%,rgba(0, 0, 0, .75) 90%);
}

.exploreHeaderImgWrapper .exploreHeaderImg{
    display:block;
}


.exploreHeaderImgTextSpan{
    display: flex;
    flex-direction: column;
    position: absolute;
    bottom: 0;
    padding: var(--container-padding);
    width: 100%;
    z-index: 1;
}
.exploreHeaderImgTextSpanUpper{
    font-size: 14px;
}

.exploreHeaderImgTextSpanLower{
    font-size: 22px;
    font-weight: 800;
}

.TrendingContainer{
    width: 100%;
}

.exploreTrendingContainerWrapper{
    border-bottom: var(--border);
}

.exploreTrendingHeaderWrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 20px 0px 20px;
    margin-bottom: 10px;
}

.exploreTrending{
    display: flex;
    flex-direction: column;
    cursor: pointer;
    padding: 10px var(--container-padding);
}
.exploreTrending:hover{

}

.exploreTrendingSpanCountry{
    font-size: 12px;
    color: var(--lightsout-text-gray);
}
.exploreTrendingSpanName{
    font-weight: 700;
    
}
.exploreTrendingSpanNoOfTweet{
    font-size: 12px;
    color: var(--lightsout-text-gray);

}


.ExploreWhatsHappening{
    width: 100%;
}

.exploreWhatsHappeningContainerWrapper{
    border-bottom: var(--border);
}

.exploreWhatsHappeningHeaderWrapper{
    padding: 10px var(--container-padding);
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* padding: 20px 20px 0px 20px; */
}


.exploreWhatsHappeningContainer{
    display: flex;
    flex-direction: column;
}

.exploreWhatsHappeningArticle{
    padding: 10px var(--container-padding);
    width: 100%;
    cursor: pointer;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.exploreWhatsHappeningArticleImg{
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: var(--br-rounded);
}


.exploreWhatsHappeningArticleText{
    display: flex;
    flex-direction: column;
    bottom: 0;
    width: 100%;
    z-index: 1;
}
.exploreWhatsHappeningArticleCategory{
    font-size: 12px;
    color: var(--lightsout-text-gray);
}

.exploreWhatsHappeningArticleTitle{
    font-weight: 700;
    overflow-wrap: break-word;
}
.NotificationsContainer{

}

.notificationsTabBarWrapper{
}

.notificationsTabBar{
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-bottom: var(--border);
}

.notificationsTabBarAllNav, .notificationsTabBarMentionsNav{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 64px;
    cursor: pointer;

}
.notificationsTabBarAllNav:hover, .notificationsTabBarMentionsNav:hover{
    background-color: var(--lightsout-container-hover-color-dark);
}

.notificationsTabBarAll, .notificationsTabBarMentions{
    color: var(--lightsout-text-gray);
}

.notificationsTabBarAll{
    color: #fff;
    font-weight: 700;
}

.notificationContainerWrapper{
    border-bottom: var(--border);
    display: flex;
    grid-gap: 16px;
    gap: 16px;
    flex-direction: row;
    padding: var(--container-padding) 0;
    cursor: pointer;
}

.notifContainerLeftWrapper{
    flex: 1 1;
    display: flex;
    justify-content: flex-end;
}
.notifContainerRightWrapper{
    flex: 8 1;
}
.notifContainerRightProfilePicWrapper{
    margin-bottom: 16px;
    display: flex;
    grid-gap: 8px;
    gap: 8px;
}
.notifContainerRightProfilePic{
    width: calc(var(--profile-pic-size) - 6px);
    border: var(--border);
}

.notifContainerRightHeaderTextWrapper{
    
}
.notifContainerRightHeaderTextUsername{
    font-weight: 700;

}
.notifContainerRightHeaderTextStatus{

}


.notifContainerRightTweetWrapper{
    display: flex;
    margin-top: 10px;
}
.notifContainerRightTweetCaption{
    color: var(--lightsout-text-gray);
    
}
.notifContainerRightTweetLink{
    margin-left: 4px;
    color: var(--lightsout-text-gray);
}

.UserUploadedPhotosWrapper{
    width: 100%;
    height: 100%;
    border-radius: var(--br-rounded);
    display: grid;
    grid-template-columns: repeat(3, 0fr);
    grid-auto-rows: minmax(auto, auto);
    grid-gap: 1px;
    gap: 1px;
    overflow: hidden;
    border: var(--border);
}
.userUploadedPhoto{
    height: 123px;
    width: 123px;
    display: inline-grid;
    border-radius: 0;
    object-fit: cover;
    cursor: pointer;
}
.ProfileContainer{

}
.profileContainerWrapper{
}


.profileTopWrapper{
    padding: 10px 20px;
    position: relative;
    font-size: 14px;
}

.profileHeaderImg{
    border-radius: 0;
    width: 100%;
    height: 100%;
}
.profileImgWrapper{
}

.profilePictureImg{
    width: 135px;
    height: 135px;
    position: absolute;
    top: -75px;
    border: 5px solid var(--lightsout-bg);
}

.profileInfoWrapper{
    margin-top: 10px;
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.editProfileBtn{
    color: #fff;
    font-weight: 700;
    padding: 10px;
    border: 1px solid #fff;
    border-radius: var(--br-circle);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.profileNameWrapper{

}

.profileName{
    font-size: 22px;
}

.profileUsername{
    font-size: 14px;
    color: var(--lightsout-text-gray);
}

.profileBio{
    display: flex;
    width: 100%;
    margin: 10px 0;
}



.profileOtherInfo{
    display: flex;
    align-items: center;
    grid-gap: 14px;
    gap: 14px;
    margin: 10px 0;
}

.profileOtherInfoCountry{
    display: flex;
    align-items: center;
    grid-gap: 4px;
    gap: 4px;
    
}

.profileOtherInfoCountryName{
    color: var(--lightsout-text-gray);
    
}

.profileOtherInfoWebsite{
    display: flex;
    align-items: center;
    grid-gap: 4px;
    gap: 4px;
}
.profileOtherInfoWebsiteLink{
    color: var(--twitter-color-main);
    text-decoration: none;
}
.profileOtherInfoWebsiteLink:hover{
    text-decoration: underline;
    
}

.profileOtherInfoJoined{
    display: flex;
    align-items: center;
    grid-gap: 4px;
    gap: 4px;

}
.profileOtherInfoJoinedDate{
    color: var(--lightsout-text-gray);
}

/* ------------------------------------ */

.profileFollowsWrapper{
    display: flex;
    align-items: center;
    grid-gap: 14px;
    gap: 14px;
}

.profileFollowing, .profileFollowers{
    cursor: pointer;
}
.profileFollowing:hover, .profileFollowers:hover{
    text-decoration: underline;
}
.profileFollowingNumber, .profileFollowersNumber{
    font-weight: 700;
}
.profileFollowingText, .profileFollowersText{
    color: var(--lightsout-text-gray);
}

.ProfileContent{

}

.profileContentTabBar{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    border-bottom: var(--border);
}

.profileContentTweetsNavWrapper,
.profileContentTweetsReplyNavWrapper,
.profileContentMediaNavWrapper,
.profileContentLikesNavWrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 64px;
    cursor: pointer;
}

.profileContentTweetsNavWrapper:hover,
.profileContentTweetsReplyNavWrapper:hover,
.profileContentMediaNavWrapper:hover,
.profileContentLikesNavWrapper:hover{
    background-color: var(--lightsout-container-hover-color-dark);
}

.profileContentTweets, .profileContentTweetsReply, .profileContentMedia, .profileContentLikes{
    color: var(--lightsout-text-gray);
}
.profileContentTweets{
    color: #fff;
    font-weight: 700;
}
